/* addition of dweeb animation */
.dweeb-enter {
  opacity: 0.01;
  transform: scale(1.1);
}

.dweeb-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 300ms;
}

.dweeb-exit {
  opacity: 1;
  transform: scale(1);
}

.dweeb-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 300ms;
}
/* addition of dweeb animation */
.page-enter {
  opacity: 0;
  transform: translateX(-100%);
}

.page-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: 300ms ease-out;
}

.page-exit {
  opacity: 1;
}

.page-exit-active {
  opacity: 0;
  transition: 300ms ease-in;
}


